import {  
	mapState,
    mapMutations  
} from 'vuex';
export default {
	data() {
		return {
			statu:{
				check1:true,
				check2:true,
			},
			currentSize:'',
		};
	},
	computed: {
		...mapState(['hasLogin','userInfo'])
	},
	onLoad(){
		
	},
	onShow() {
		this.load();
	},
	methods:{
		...mapMutations(['logout']),

		navTo(url){
			uni.navigateTo({
				url:url
			})
		},
		async load(){
			var that=this;
			uni.getStorage({
				key:'switch',
				success:function(res1){					
					that.statu.check1=res1.data;
				}
			})
			uni.getStorage({
				key:'play',
				success:function(res2){
					that.statu.check2=res2.data;
				}
			})
			const res = uni.getStorageInfoSync();
			console.log(res,"res3");
			console.log(res.currentSize,"currentSize");
			var size=res.currentSize;
			if(size>=1024){
				var size=(size/1024).toFixed(2);
				that.currentSize=size+" M";
			}else{
				that.currentSize=size+" KB";
			}
		},
		//退出登录
		toLogout(){				
		/* 	console.log(this.hasLogin,"00000") */
			if(this.hasLogin){
				uni.showModal({
				    content: '确定要退出登录么',
				    success: (e)=>{
				    	if(e.confirm){
				    		this.logout();
				    		setTimeout(()=>{
				    			uni.switchTab({
				    				url:'/pages/GRZX/user'
				    			})
				    		}, 200)
				    	}
				    }
				});
			}else{
				uni.showToast({
					title : '请先登录',
					icon : 'none',
				})
				/* setTimeout(function(){
					uni.navigateTo({
						url  : '/pages/GRZX/userLogin'
					}) 
				},1500); */
			}
			
		},
		//清除缓存
		clearStorage(){
			var user=uni.getStorageSync('userInfo');
			uni.showModal({
			    content: '是否清除数据',
			    success: (e)=>{
			    	if(e.confirm){
						uni.clearStorage();
						uni.setStorageSync('userInfo',user);
						uni.redirectTo({
							url:'/pages/GRZX/set'
						})
			    	}
			    }
			});
		},
		//switch
		switchChange(e){
			let statusTip = e.detail.value ? true: false;
			uni.setStorage({
				key:'switch',
				data:statusTip
			})
			//console.log(statusTip)
			//this.$api.msg(`${statusTip}消息推送`);
		},
		playChange(e){
			let statusTip = e.detail.value ? true: false;
			uni.setStorage({
				key:'play',
				data:statusTip
			})
			//console.log(statusTip)
			//this.$api.msg(`${statusTip}消息推送`);
		},

	}
}
